* {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
}

body {
    background-color: #292524;
    color: #fafaf9;
    font-family:"Sono", sans-serif;
    padding: 48px 64px 0px;
}

.hidden {
    display: none !important;
}

.message {
    text-align: center;
    text-transform: uppercase;
    font-size: 32px;
    font-weight: 600;
    margin-top: 24px;
}

.disputed {
    color: #ef4444;
    font-weight: 600px;
    margin-right: 20px;
}

/* MAIN LAYOUT */
#root {
    max-width: 1500px;
    margin: 0 auto;
}

.header {
    margin-bottom: 40px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}

.main {
    display: grid;
    grid-template-columns: 250px 1fr;
    gap: 48px;
    height: calc(100vh - 48px - 68px - 40px);
}

@media (max-width: 900px) {
    .main {
        grid-template-columns: 1fr;
    }
}

section {
    overflow: scroll;
    padding-bottom: 40px;
}

@media(max-width:900px) {
    section {
        height: 40vh;
    }
}


/* ELEMENTS */
.logo {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    gap: 16px;
}

.logo img {
    width: 68px;
    height: 68px;
}

h1 {
    font-size: 42px;
    text-transform: uppercase;
    font-family: "Coiny", sans-serif;
    line-height: 1;
    margin-top: 6px;
}

.category {
    margin-bottom: 16px;
}

.fact {
    font-size: 26px;
    line-height: 1.4;
    background-color: #44403c;
    margin-bottom: 16px;
    padding: 16px 24px;
    letter-spacing: -1px;
    border-radius: 16px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    gap: 24px;
}

@media (max-width: 1200px) {
    .fact {
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
        -webkit-box-align: end;
            -ms-flex-align: end;
                align-items: flex-end;
        gap: 12px;
    }
}

.tag {
    text-transform: uppercase;
    font-size: 14px;
    font-family: "Coiny", sans-serif;
    padding-top: 3px;
    padding-left: 10px;
    padding-right: 10px; 
    border-radius: 100px;
}

.vote-buttons {
    margin-left: auto;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    gap: 8px;
}

.vote-buttons button {
    border: none;
    background-color: #78716c;
    font-size: 18px;
    padding: 6px 12px;
    border-radius: 100px;
    color: inherit;
    font-family: inherit;
    font-weight: 600;
    cursor: pointer;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

.vote-buttons button:hover {
    background-color: #292524;
}

.vote-buttons button:disabled {
    background-color: #44403c;
}

ul {
    list-style: none;
}

.source:link,
.source:visited {
    color: #a8a29e;
    text-decoration: none;
    margin-left: 12px;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

.source:hover,
.source:active {
    color: #3b82f6;
}

.btn {
    border: none;
    font-family: "Coiny", sans-serif;
    line-height: 1;
    text-transform: uppercase;
    font-size: 17px;
    padding: 16px 0 13px;
    background-image: -o-linear-gradient(315deg, #db2777, #14b8a6, #f97316, #8b5cf6);
    background-image: linear-gradient(135deg, #db2777, #14b8a6, #f97316, #8b5cf6);
    color: inherit;
    border-radius: 100px;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    cursor: pointer;
    transition: 0.3s;
}

.btn:hover {
    -webkit-transform: scale(110%) rotate(-2deg);
        -ms-transform: scale(110%) rotate(-2deg);
            transform: scale(110%) rotate(-2deg);
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

.btn-large {
    font-size: 20px;
    padding: 20px 30px 17px;
}

.btn-all-categories {
    margin-bottom: 16px;
    width: 100%;
}

.btn-category {
    width: 100%;
    background-image: none;
}

/* FORM */
.fact-form {
    background-color: #44403c; 
    margin-bottom: 40px;
    padding: 16px 32px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    gap: 16px;
    border-radius: 16px;
}

.fact-form input,
.fact-form select {
    width: 220px;
    background-color: #78716c;
    border: none;
    border-radius: 100px;
    padding: 16px;
    font-size: 16px;
    color: inherit;
    font-family: inherit;
}

.fact-form input::-webkit-input-placeholder {
    color: #a8a29e
}

.fact-form input::-moz-placeholder {
    color: #a8a29e
}

.fact-form input:-ms-input-placeholder {
    color: #a8a29e
}

.fact-form input::-ms-input-placeholder {
    color: #a8a29e
}

.fact-form input::placeholder {
    color: #a8a29e
}

.fact-form input:first-child {
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
}

.fact-form span {
    font-weight: 600;
    font-size: 18px;
    margin-right: 18px;
}

@media (max-width: 1000px) {
    body {
        /* padding: 32px 48px; */
    }

    .fact-form {
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
        -webkit-box-align: stretch;
            -ms-flex-align: stretch;
                align-items: stretch;
    }
    

    .fact-form input,
    .fact-form select {
        width: auto;
    }
}